@import url(https://fonts.googleapis.com/css?family=Inconsolata);
body {
  margin: 0;
  padding: 0;
  background-color: #f9f9f9;
  font: 10px 'Inconsolata', sans-serif;
  -webkit-font-smoothing: antialiased; }

h1, h2, h3, h4 {
  font-family: 'Inconsolata', sans-serif;
  margin: 0; }

h1 {
  font-size: 3rem; }

h2 {
  font-size: 2.25rem; }

h3 {
  font-size: 1.75rem; }

h4 {
  font-size: 1.125rem; }

* {
  box-sizing: border-box; }

.container {
  width: 80%;
  margin: 0 auto; }

.font--bold {
  font-weight: bold; }

.Sidebar__container {
  background-color: steelblue; }
  @media (min-width: 900px) {
    .Sidebar__container {
      width: 40%;
      position: fixed;
      min-height: 100%; } }

.Menu {
  text-align: center;
  padding: 40px 50px 10px; }
  @media (min-width: 1300px) {
    .Menu {
      display: flex;
      justify-content: space-between;
      text-align: inherit;
      padding: 90px 50px 10px 70px; } }

.Menu__title {
  font-size: 3rem;
  line-height: 2.5rem; }
  @media (min-width: 1300px) {
    .Menu__title {
      font-size: 2rem;
      line-height: inherit; } }

.Menu__list {
  margin-top: 40px; }
  @media (min-width: 1300px) {
    .Menu__list {
      margin-top: 11px;
      float: right; } }
  .Menu__list a {
    text-decoration: none;
    padding: 6px 20px;
    font-size: 1rem;
    border: 1px solid #003c50;
    margin: 7px;
    color: #003c50; }

.Intro {
  padding: 40px 5vw;
  background-color: steelblue; }
  @media (min-width: 600px) {
    .Intro {
      padding: 40px 50px 40px 70px; } }

.Intro__list {
  margin: 30px 20px;
  padding: 0;
  list-style-type: square; }

.Intro__heading {
  text-align: center;
  font-size: 1.3rem; }
  @media (min-width: 1200px) {
    .Intro__heading {
      font-size: 1.75rem; } }
  @media (min-width: 900px) {
    .Intro__heading {
      text-align: inherit; } }

.Intro__list-item {
  font-size: 1rem;
  position: relative;
  margin-top: 10px; }
  @media (min-width: 1200px) {
    .Intro__list-item {
      font-size: 1.2rem; } }

.TimelineCard__container {
  position: relative;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
  margin: 0 auto;
  opacity: 0;
  width: 90%; }
  @media (min-width: 1200px) {
    .TimelineCard__container {
      width: 50%; } }
  @media (min-width: 1200px) {
    .TimelineCard__container.js-expand-card {
      width: 60%; } }
  .TimelineCard__container.js-show-card {
    opacity: 1; }
    .TimelineCard__container.js-show-card .TimelineCard__joiner {
      opacity: 1; }

.TimelineCard__stack {
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
  position: relative;
  z-index: 100;
  margin-top: -12px; }

.TimelineCard {
  width: 100%;
  background-color: #FFF;
  border: 1px solid #e9e9e9;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
  box-shadow: 0 20px 2px -10px rgba(0, 0, 0, 0.02);
  display: inline-block; }

.TimelineCard__top-card {
  font-size: 1rem;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
  position: relative;
  z-index: 1;
  padding: 25px 15px 20px 15px; }

.TimelineCard__bottom-card {
  height: 0;
  opacity: 0;
  top: 0;
  right: 0; }
  .TimelineCard__container.js-expand-card .TimelineCard__bottom-card {
    padding: 20px;
    opacity: 1;
    top: 15px;
    height: 100%; }

.TimelineCard__description {
  font-size: 1rem;
  line-height: 1.5rem;
  -webkit-transition-property: width, height, opacity;
  transition-property: width, height, opacity;
  -webkit-transition-duration: 0.5s, 0.5s, 0.5s;
          transition-duration: 0.5s, 0.5s, 0.5s;
  text-align: justify;
  overflow: hidden;
  opacity: 0;
  height: 0;
  margin: 0; }
  .js-expand-card .TimelineCard__description {
    opacity: 1;
    height: 100%; }

.TimelineCard__details {
  float: left;
  width: 65%;
  margin-left: 20px; }

.TimelineCard__techlist {
  float: left;
  clear: both;
  list-style: none;
  padding: 0;
  opacity: 0;
  margin-top: 5px;
  -webkit-transition: opacity 2s;
  transition: opacity 2s; }
  .js-expand-card .TimelineCard__techlist {
    opacity: 1; }

.TimelineCard__title {
  margin-bottom: 10px;
  font-size: 1.5rem; }

.TimelineCard__locations {
  color: lightgray;
  margin: 0;
  padding: 0;
  list-style: none; }

.TimelineCard__dates {
  margin-bottom: 10px; }

.TimelineCard__joiner {
  width: 7px;
  height: 100px;
  background-color: steelblue;
  margin: -15px auto 0;
  opacity: 0;
  -webkit-transition: all 1s;
  transition: all 1s; }

.TimelineCard__joiner-date {
  font-size: 1rem;
  margin-left: 25px;
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
  padding: 40px 10px;
  position: relative; }

.TimelineCard__image {
  width: 20%;
  height: auto;
  margin-top: 5px;
  display: inline-block; }
  @media (min-width: 600px) {
    .TimelineCard__image {
      margin-left: 45px; } }
  .TimelineCard__image img {
    width: 100%;
    height: auto; }

.Techlist {
  margin: 0;
  padding-left: 10px;
  list-style: none; }

.Techlist .Techlist__item {
  float: left;
  padding: 6px; }
  @media (min-width: 1200px) {
    .Techlist .Techlist__item {
      padding: 10px; } }
  .Techlist .Techlist__item img {
    width: auto;
    height: 35px; }
    @media (min-width: 1200px) {
      .Techlist .Techlist__item img {
        height: 75px; } }

.Timeline {
  margin: 70px auto 400px; }
  @media (min-width: 900px) {
    .Timeline {
      margin-top: 100px;
      width: 60%;
      float: right; } }

